import * as types from '../types'

const state = {
	payload: undefined,
	DOMObject: undefined,
	indexes: Array,
	index_counter: 0,
	currentOption: undefined,

	options: {
		'İşyeri veri seti seçin': undefined,
		'Finartz Kuştüyü': 'kustuyu.json',
		'Finartz Web Servis': 'webservis.json',
		'BKM Default': 'bkmdefault.json',
		'Finart Gerçek Banka': 'gercekbanka.json'
	}
}

const mutations = {
	[types.SET_PAYLOAD] (state, data) {
		//console.log("SET_PAYLOAD data ", data)
		if (typeof data === "string")
			data = JSON.parse(data)
		//data = JSON.parse(data)
		state.payload = data
		//state.DOMObject = Object.assign({}, data)
		state.DOMObject = JSON.parse(JSON.stringify(data))
		//console.log("SET_PAYLOAD DOMObject ", state.DOMObject)
		deflate(state.DOMObject)
		//state.indexes = _indexes
		//console.log("SET_PAYLOAD deflated object ", state.DOMObject)
	},

	[types.SET_CURRENT_OPTION] (state, option) {
		//console.log("SET_CURRENT_OPTION option ", option)
		state.currentOption = state.options[option]
	},

	[types.SET_PASTE_OPTION] (state) {
		state.currentOption = 'PASTE_EVENT'
	},
	[types.UPDATE_VALUE] (state, {value, id}) {
		inflate(value, id)
	}
}

class Box {
	constructor(value, path) {
		this.value = value
		this.path = path
	}
}

//var _indexes = []
var path = ""

function isObjectOrArray(obj) {
  return (obj && (typeof obj === "object")) || Array.isArray(obj)
}

function isEmpty(obj) {
  return Object.keys(obj).length == 0
}

var newObj = {}

function deflate(obj) {
  
  for (var o in obj) {
    if (!isObjectOrArray(obj[o])) {
      //console.log("obj is not object or array. o is ", o)
      var p = path + '-' + o // path without its value
      //p = path + '/' + o + ' = ' + obj[o] // path with its value
      //console.log("now path is ", path, " and p ", p)
      //_indexes.push(p)
      //obj[o] = obj[o] + '|' + p // in-place replace
      //newObj[o] = obj[o] + '|' + p
      //console.log("path is ", path)
      obj[o] = new Box(obj[o], p)
      //obj[o] = {'value': obj[o], path: p}
      //obj[o] = new Box(p, obj[o])
      //path = ""
    } else {
      //console.log("is object or array. o is ", o)
      path = path + '-' + o
      //console.log("path in 'object or array' block ", path)
      //console.log("object path ", path)
      obj[o]['__fullpath__'] = path.slice(1, path.length)
      //Object.definePropery(obj[o], '__fullpath__', {
      //	value: path.slice(1, path.length)
      //})
      //console.log("__fullpath__ is ", obj[o].__fullpath__)
      deflate(obj[o])
      //path = ""
      //console.log("path before ", path)
      var arr = path.split('-')
      path = arr.slice(0, arr.length-1).join('-')
      //console.log("path after ", path)
    }
  }
  
  //p = path + '/' + o
  //indexes.push(p)
  
  //console.log("I\'m done!!")
}


