//  	//console.log(obj[indexes[i]])
//  	if (i == indexes.length - 1) {
//  		console.log("old value ", obj[indexes[i]])
//  		var path = obj[indexes[i]].split('|')[1]
//  		obj[indexes[i]] = value + '|' + path
//  		return
//  	}
//  }
