function inflate(value, id) {
	//console.log("id is ", id)
	//console.log("value is ", value)
	var indexes = id.split('-').filter(function(val) {
		if (val !== "") return val
	})
	//console.log("indexes ", indexes)
	//obj = state.payload
	var stmt = "state.payload"
	var stmt2 = "state.DOMObject"
	for (var i in indexes) {
		stmt += "['" + indexes[i] + "']"
		stmt2 += "['" + indexes[i] + "']"
	}
	stmt2 += "['value']" + "='" + value + "'"
	stmt += "= '" + value + "'"
	//stmt += "['value']" + "='" + value + "'"
	//console.log("stmt is ", stmt)
	//console.log("stmt2 is ", stmt2)
	//console.log("stmt eval ", eval(stmt))
	eval(stmt)
	eval(stmt2)
}

export default {
	state,
	mutations
}


