<template>
<li>
    <div v-for="(val, key) in model" v-if="!isObject(val)">
  		<li v-show="key !== '__fullpath__'">
        	<label>{{ key }}</label>
        	<input class="form-control" :id="val.path" :value="val.value" @input="updateState">
		</li>
    </div>
    <ul v-else class="left list-unstyled">
    	<div style="display: table">
    		<div style="display: table-row">
    			<div style="display: table-cell">
      				<button v-if="Number.isInteger(key)" type="button" class="btn btn-info" data-toggle="collapse" :data-target="'#' + val.__fullpath__.value" style="margin: 5px;">{{ key + 1 }}</button>
      				<button v-else type="button" class="btn btn-info" data-toggle="collapse" :data-target="'#' + val.__fullpath__.value" style="margin: 5px;">{{ key }}</button>
      			</div>
      			<div style="display: table-cell">
      				<div style="display: inline-block;" v-for="(v,k) in val" v-if="isProperField(k,v)">
      					<div style="outline: 1px solid #ccc; background: #fffafa">
      						<strong>{{ k }}:</strong>
      						<p style="display: inline;">{{ v.value }}</p>
      					</div>
      				</div>
      			</div>
      		</div>
      </div>
      <payload :id="val.__fullpath__.value" class="collapse" :model="val"></payload>
    </ul>
</li>
</template>

<script>
export default {

  name: 'payload',

  props: {
  	model: Object,
  },

  methods: {
  	isObject: function(obj) {
  		//this.$store.commit('INCREMENT_INDEX_COUNTER')
  		//console.log("isObject obj is ", obj)
  		return !obj.hasOwnProperty("path")
  		//return (!(obj instanceof Box) && (obj instanceof Object)) || Array.isArray(obj)
  	},

  	isContainArray: function(key, subkey, obj) {
  		return JSON.stringify(obj).includes('[')
  	},

  	isProperField: function(k,v) {
  		//this.flatten(v)
  		return !Number.isInteger(k) && (k !== '__fullpath__') && !this.isObject(v)
  	},

  	updateState: function(e) {
  		//this.model[e.target.id] = e.target.value
  		//console.log("value ", this.$store.state.payload.payload)
  		//console.log("id and value ", e.target.id, ' ', e.target.value)
  		//var value = e.target.value + '|' + e.target.id
  		var id = e.target.id
  		var value = e.target.value
  		this.$store.commit('UPDATE_VALUE', {value, id})
  	}
  },

  /*
  created () {
  	console.log("created")
  },

  beforeCreate () {
  	console.log("beforeCreate")
  },

  beforeMount () {
  	console.log("beforeMount")
  },

  mounted () {
  	console.log("mounted")
  },

  beforeUpdate () {
  	console.log("beforeUpdate")
  },

  updated () {
  	console.log("updated")
  },

  activated () {
  	console.log("activated")
  },

  deactivated () {
  	console.log("deactivated")
  },

  beforeDestroy () {
  	console.log("beforeDestroy")
  },

  destroyed () {
  	console.log("destroyed")
  }
  */

};
</script>

<style scoped>
</style>


