import * as types from './types'
import Vue from 'vue'

export const loadDOMObject = ({ commit, getters }) => {
  //console.log("loadPayload")
  if (getters.getCurrentOption !== undefined) {
    Vue.http({
        url: '/' + getters.getCurrentOption,
        method: 'GET'
      }).then(function(response) {
        commit(types.SET_PAYLOAD, response.data)
      }, function(err) {
        console.log("status ", err.statusText)
      })
  }
}

export const setCurrentOption = ({ commit, getters }, {target: { value }}) => {
  commit(types.SET_CURRENT_OPTION, value)
  loadDOMObject({commit, getters})
}

export const setPasteOption = ({ commit }) => {
  commit(types.SET_PASTE_OPTION)
}

export const updatePayload = ({ commit }, payload) => {
  commit(types.UPDATE_PAYLOAD, payload)
}

export const loadCustomPayload = ({ commit, getters}, data) => {
  //console.log("loadCustomPayload")
  setPasteOption({ commit })
  commit(types.SET_PAYLOAD, data)
}


