<template>
    <div @paste.stop="pasteEvent">
    <strong class="description">Flow 2.0 Merchant Simulasyonu</strong>
    <select style="padding: 10px; margin: 10px; width: 250px" @change="setCurrentOption">
      <option v-for="(file, option) in options" :value="option">{{ option }}</option>
    </select>
    <ul class="left list-unstyled" v-if="treeData !== undefined">
      <payload :model="treeData">
      </payload>
    <button type="button" class="btn btn-primary btn-lg" @click="send">Ödeme İşlemi Başlat</button>
    </ul>
    </div>
</template>

<script>
import Payload from './components/Payload'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'app',
  components: {
    Payload
  },

  computed: {
    ...mapGetters({
      treeData: 'getDOMObject',
      options: 'getOptions'
    })
  },

  methods: {
    send: function() {
        //console.log("new payload ", this.$store.state.payload.payload)
        this.$http.post('/custom/init', this.$store.state.payload.payload, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          }
        }).then((response) => {
          console.log("response success ", response)
          //console.log("array ", JSON.parse(response.body.ticketArray))
          start(JSON.parse(response.body.ticketArray))
        }, (response) => {
          console.log("response error ", response.statusText)
        })
    },

    pasteEvent: function(e) {
      this.$store.dispatch('loadCustomPayload', e.clipboardData.getData("text"))
    },

    ...mapActions([
      'setCurrentOption'
      ])
  }
}
</script>

<style>

* {
  margin: 2px;
  padding: 2px;
}

ul {
  border-bottom: thick solid #ccc;
  border-top: thick solid #ccc;
  border-right: thick solid #ccc;
  margin: 5px;
}

li {
  margin: 5px;
}

ul.left {
  border-left: thick solid #ccc
}

ul.right {
  border-right: thick solid #ccc
}

button {
  margin: 5px;
}

.description {
  margin: 5px;
}

</style>



